<div class="dashboard"><el-scrollbar class="pst--scroll main--scroll" height="370px"><div class="c-card"><div class="c-card__item no-pd"><div class="c-card__inner"><div class="calendar"><v-date-picker v-model="date" :first-day-of-week="2" locale="en" is-expanded is-range color="pink"></v-date-picker></div></div></div><div class="c-card__item"><div class="c-card__inner"><div class="l-member"><el-scrollbar class="pst--scroll" height="138px"><ul class="l-member__list"><li class="l-member__item" v-for="item of memberList"><div class="l-member__inner"><p class="l-member__photo"><mdicon name="account" size="15"></mdicon></p><strong class="l-member__name">{{ item.name }}</strong><span class="l-member__date">{{ item.date }}</span></div></li></ul></el-scrollbar></div></div></div></div><div class="c-card"><div class="c-card__item" v-for="(item,index) of cardList"><div class="c-card__inner"><strong class="c-card__title">{{ item.name }}</strong><div class="c-card__type--number"><strong>{{ item.number }}</strong> <span>{{ item.type }}</span></div><div class="c-card__type--button"><button class="btn--up" @click="countUp(index)"><mdicon name="chevron-up" size="15"></mdicon></button><button class="btn--down" @click="countDown(index)" :disabled="item.number==item.minNum"><mdicon name="chevron-down" size="15"></mdicon></button></div></div></div></div><div class="c-card"><div class="c-card__item no-pd col-1"><div class="c-card__inner"><el-table :data="tableData" height="130" style="width:100%"><el-table-column prop="name" align="center" label="Name" width="100"></el-table-column><el-table-column prop="date" align="center" label="Date"></el-table-column></el-table></div></div></div></el-scrollbar></div>